import './App.css';
import { useState, useEffect } from 'react';
import CaseStudies from './components/CaseStudies';
import AIProjects from './components/AIProjects';
import ResumeModal from './components/ResumeModal';
import Contact from './components/Contact';
import AIProjectModal from './components/AIProjectModal';
import { initGA, logPageView, logEvent } from './utils/analytics';

function App() {
  const [activeTab, setActiveTab] = useState('home');
  const [showResume, setShowResume] = useState(false);
  const [showAIProject, setShowAIProject] = useState(false);

  const portfolioProject = {
    id: 1,
    title: "AI-Assisted Portfolio Development",
    content: {
      overview: {
        title: "Project Overview",
        text: "I built this website with Cursor, utilizing collaborative coding with Claude 3.5 Sonnet, an Anthropic LLM. The project demonstrates how AI can be effectively used as a programming partner while maintaining full developer control over architecture and design decisions."
      },
      technical: {
        title: "Technical Implementation",
        text: "Built with React and modern CSS, the site features responsive design, component-based architecture, and clean, maintainable code. Key features include:",
        list: [
          "Dynamic content rendering with React components",
          "Markdown-based case study system",
          "Modal windows for detailed content",
          "Responsive video embedding",
          "CSS Grid and Flexbox layouts"
        ]
      },
      process: {
        title: "AI Collaboration Process",
        text: "Claude served as a programming partner throughout development, helping with:",
        list: [
          "Code implementation and debugging",
          "CSS styling and layout solutions",
          "Component structure recommendations",
          "Best practice suggestions",
          "Real-time code reviews and improvements"
        ]
      },
      learnings: {
        title: "Key Learnings",
        text: "This project demonstrated how AI can enhance the development process while maintaining high code quality:",
        list: [
          "AI excels at suggesting implementation details while humans drive overall architecture",
          "Iterative AI collaboration enables rapid prototyping and refinement",
          "Clear communication with AI leads to better code solutions",
          "AI can help maintain consistency across a codebase",
          "Human oversight remains crucial for final design and implementation decisions"
        ]
      }
    }
  };

  useEffect(() => {
    initGA();
    logPageView();
  }, []);

  const handleTabChange = (tab) => {
    setActiveTab(tab);
    logEvent('Navigation', `Viewed ${tab} page`);
  };

  const renderContent = () => {
    switch(activeTab) {
      case 'case-studies':
        return <CaseStudies />;
      case 'ai-projects':
        return <AIProjects />;
      case 'contact':
        return <Contact onShowResume={() => setShowResume(true)} />;
      case 'home':
      default:
        return (
          <div className="content-container">
            <div className="video-container">
              <div className="video-wrapper">
                <iframe 
                  src="https://player.vimeo.com/video/1049879948?title=0&amp;byline=0&amp;portrait=0&amp;badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479"
                  width="1080"
                  height="1920"
                  frameBorder="0"
                  allow="autoplay; fullscreen; picture-in-picture; clipboard-write; encrypted-media"
                  title="M"
                  style={{position: 'absolute', top: 0, left: 0, width: '100%', height: '100%'}}
                ></iframe>
              </div>
            </div>
            <div className="bio-section">
              <div className="bio-content">
                <h2>Entrepreneurial Product Leader</h2>
                <p>
                  I build and scale AI-powered platforms that transform how work gets done. With experience launching category-defining products from 0-to-1 across startup and enterprise environments, I specialize in turning complex business challenges into intelligent automated solutions that drive rapid user adoption and measurable business value. I combine founder vision with enterprise platform expertise and hands-on AI implementation experience to accelerate the development of vertical automation solutions that enhance both business outcomes and employee experience.
                </p>
                <p>
                  I built this site with Cursor using Claude as a coding assistant.{' '}
                  <span 
                    className="learn-more-link"
                    onClick={() => setShowAIProject(true)}
                  >
                    Learn More
                  </span>
                </p>
              </div>
            </div>
          </div>
        );
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <div className="header-container">
          <h1>Mimi Leinbach</h1>
          <nav className="tab-menu">
            <ul>
              <li>
                <button 
                  className={`tab-button ${activeTab === 'home' ? 'active' : ''}`}
                  onClick={() => handleTabChange('home')}
                >
                  Home
                </button>
              </li>
              <li>
                <button 
                  className={`tab-button ${activeTab === 'ai-projects' ? 'active' : ''}`}
                  onClick={() => handleTabChange('ai-projects')}
                >
                  AI Projects
                </button>
              </li>
              <li>
                <button 
                  className={`tab-button ${activeTab === 'case-studies' ? 'active' : ''}`}
                  onClick={() => handleTabChange('case-studies')}
                >
                  Case Studies
                </button>
              </li>
              <li>
                <button 
                  className={`tab-button ${activeTab === 'contact' ? 'active' : ''}`}
                  onClick={() => handleTabChange('contact')}
                >
                  Contact
                </button>
              </li>
            </ul>
          </nav>
        </div>
      </header>
      {renderContent()}

      {showResume && (
        <ResumeModal 
          key={Date.now()}
          onClose={() => setShowResume(false)} 
        />
      )}
      
      {showAIProject && (
        <AIProjectModal
          project={portfolioProject}
          onClose={() => setShowAIProject(false)}
        />
      )}
    </div>
  );
}

export default App;
